<template>
  <div class="app-container">
    <div class="head-bg"></div>
    <van-list
      v-model="loading"
      :finished="finished"
      @load="onLoad"
      finished-text=" "
      class="content"
    >
      <div
        class="item"
        v-for="(item, index) in agentsList"
        :key="index"
        @click.stop="routerPush(item)"
      >
        <div class="image">
          <img :src="item.mobile_thumb_url" alt />
        </div>
        <div class="font">{{ item.title }}</div>
      </div>
    </van-list>
    <div class="footer">上海岐素信息科技有限公司</div>
  </div>
</template>

<script>
import { agents } from "@/api/user";
import jump from "@/utils/openWindow";
export default {
  components: {},
  props: {},
  data() {
    return {
      agentsList: [],
      loading: false,
      finished: false,
      listQuery: {
        keyword: "",
        page: 1,
        count: 10,
        // order: "",
        sort: "",
        type: "",
      },
      total: 0,
    };
  },
  watch: {},
  computed: {},
  methods: {
    async onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      // 加载状态结束
      this.getUserInfo();
      // 数据全部加载完成
    },
    async getUserInfo() {
      let res = await agents(this.listQuery);
      this.agentsList = [...this.agentsList, ...res.data];
      console.log(this.agentsList);
      this.total = res.meta.total;
      this.listQuery.page++;
      this.loading = false;
      if (
        this.listQuery.page >= Math.ceil(res.meta.total / this.listQuery.page)
      ) {
        this.agentsList.push({
          title: "个人中心",
          mobile_thumb_url: `../../../../static/image/mobile/modile_health.jpg`,
        });
        this.finished = true;
      }
    },
    routerPush(row) {
      if (row.redirect_url) {
        jump(row.redirect_url);
      } else {
        this.$router.push({ name: "m_personal" });
      }
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.app-container {
  width: 100%;
  min-height: 100%;
  position: relative;
  padding-bottom: 65px;
  box-sizing: border-box;
  background-color: #f9f9f9;
  /deep/ .van-list__loading,
  /deep/ .van-list__placeholder,
  /deep/ .van-list__finished-text {
    display: block;
    width: 100%;
  }
  .head-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 160px;
    background: url("../../../../static/image/mobile/account_head.png")
      no-repeat;
    background-size: 100% 100%;
  }
  .content {
    position: inherit;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 60px 20px 0;
    box-sizing: border-box;
    .item {
      width: 48%;
      height: 100px;
      margin-bottom: 4%;
      background-color: #ffffff;
      box-shadow: 0px 0px 20px 0px rgba(131, 200, 244, 0.13);
      border-radius: 10px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      .image {
        width: 45px;
        height: 45px;
        margin-right: 10px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .font {
        min-width: 64px;
        font-size: 1rem;
      }
    }
  }
  .footer {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 65px;
    line-height: 65px;
    box-sizing: border-box;
    text-align: center;
    color: #999999;
    font-size: 0.9rem;
    opacity: 0.9;
  }
}
</style>


